.mega-nav-wrapper {
  .mobile-utility {
    display: none;

    @include maxbp($mega-menu-mobile) {
      display: block;
    }

    .utility-wrapper {
      display: block;

      .utility.header-container {
        display: block;
        padding: 0;

        ul {
          display: block;

          li {
            display: block;
            margin: 0;

            &.utility-signin {
              a {
                display: block;
                max-width: none;
                text-align: left;
                color: white;
                text-transform: uppercase;
                text-decoration: none;

                picture.icon-img__image {
                  display: inline-block;
                  vertical-align: middle;
                }

                &::after {
                  content: "Sign in";
                  display: inline-block;
                  vertical-align: middle;
                }
              }
            }

            &.utility-cart {
              display: none;
            }
          }
        }
      }
    }
  }
}
