.mega-nav-wrapper {
  .mega-menu-nav {
    @include breakpoint($mega-menu-mobile) {
      position: relative;

      > li {
        > ul {
          top: 51px;
        }
      }

      &:not(.mega-menu-nav--sub) {
        background: none;

        > li {
          > a {
            &::after {
              background: $delta-red;
              height: 4px;
              width: 0;
              content: "";
              display: block;
              position: absolute;
              left: 0;
              bottom: 0;
              transition: all ease 0.5s;
            }

            &.active,
            &:active,
            &:hover {
              &::after {
                width: 100%;
              }
            }
          }
        }
      }

      li.mega-menu-nav__item--with-sub {
        //position: relative;
        > .mega-menu-nav--sub-1 {
          background: white;
        }

        &:hover {
          .mega-menu-nav--sub-1 {
            .mega-menu-nav__item--sub {
              display: inline-block;
            }
          }
        }

        .mega-menu-nav--sub-1 {
          position: absolute;
          width: 100%;
          left: 0;
          background: $white;
          z-index: 10000;

          .mega-menu-nav__item--sub {
            display: none;

            a {
              display: inline-block;
            }
          }
        }
      }

      display: block;
      margin: 0;
      padding: 0;

      &__item {
        display: inline-block;
        letter-spacing: normal;
        margin: 0 $space-ten*2 0 0;

        @include maxbp($big-desktop) {
          margin: 0 $space-ten/2 0 0;
        }
      }

      &__link {
        text-decoration: none;
        font-size: 18px;
        font-weight: normal;
        display: block;
        color: $gray-10;
        position: relative;
        line-height: 1.6;
        padding: 7px $space-ten $space-ten+4 $space-ten;

        @include maxbp($big-desktop) {
          padding: $space-ten+2 $space-ten/2 $space-ten+4;
          font-size: 15px;
        }
      }
    }
  }
}
