.grid-example {
  margin: 0 0 $space-ten*3;

  div {
    background-color: rgba(0, 0, 0, 0.2);
    line-height: 50px;
    text-align: center;

    &:nth-child(even) {
      background-color: rgba(0, 0, 0, 0.1);
    }
  }
}

[class*=gridcol-] {
  width: 100%;
  padding-right: 24px;
  padding-left: 24px;
}

[class*=grid-sm-col-] {
  width: 100%;
  padding-right: 24px;
  padding-left: 24px;

  @include maxbp($grid-lg) {
    padding-right: $space-ten;
    padding-left: $space-ten;
  }

  @include maxbp($grid-sm) {
    max-width: none !important;
    flex: 0 0 100% !important;
  }
}

[class*=grid-md-col-] {
  width: 100%;
  padding-right: 24px;
  padding-left: 24px;

  @include maxbp($grid-lg) {
    padding-right: $space-ten*2;
    padding-left: $space-ten*2;
  }

  @include maxbp($grid-md) {
    max-width: none !important;
    flex: 0 0 100% !important;
  }
}

[class*=grid-lg-col-] {
  width: 100%;
  padding-right: 24px;
  padding-left: 24px;

  @include maxbp($grid-lg) {
    max-width: none !important;
    flex: 0 0 100% !important;
  }
}

