@mixin arrow-link($size, $arrow-size, $arrow-space, $color) {
  text-transform: uppercase;
  color: $color;
  text-decoration: none;
  position: relative;
  padding: 0 $arrow-space 0 0;
  font-size: $size;
  font-weight: bold;
  letter-spacing: 2px;
  margin: 0 0 $space-ten;

  &:hover {
    color: $gray-10;

    &::after {
      border-color: transparent transparent transparent $gray-10;
    }
  }

  &::after {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: $arrow-size 0 $arrow-size $arrow-size+2;
    border-color: transparent transparent transparent $color;
    content: "";
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
  }
}

.xsm-red-arrow-link {
  @include arrow-link(12px, 4px, 10px, $delta-red);
}

.sm-red-arrow-link {
  @include arrow-link(14px, 4px, 10px, $delta-red);
}

.md-red-arrow-link {
  @include arrow-link(16px, 5px, 15px, $delta-red);
}

.lg-red-arrow-link {
  @include arrow-link(18px, 7px, 15px, $delta-red);
}

.xsm-punch-arrow-link {
  @include arrow-link(12px, 4px, 10px, $delta-punch);
}

.sm-punch-arrow-link {
  @include arrow-link(14px, 4px, 10px, $delta-punch);
}

.md-punch-arrow-link {
  @include arrow-link(16px, 5px, 15px, $delta-punch);
}

.lg-punch-arrow-link {
  @include arrow-link(18px, 7px, 15px, $delta-punch);
}

.arrow-link-list {
  margin: 0 0 $space-ten;
}
