//  Empty
.header-wrapper {
  @include wrapper;

  border-bottom: 1px solid $gray-70;
  display: block;
  min-height: 61px;

  @include maxbp($mega-menu-mobile) {
    position: relative;
    min-height: 10px;
    padding: 0;
  }

  .region-site-branding {
    float: left;

    @include maxbp($mega-menu-mobile) {
      float: none;
    }

    .logo {
      margin: $space-ten 0;

      @include maxbp($mega-menu-mobile) {
        float: none;

        img {
          margin: auto;

          @include maxbp($small) {
            width: 140px;
          }

          @include maxbp(400px) {
            width: 120px;
          }
        }
      }
    }

    #block-cart-2 {
      display: none;

      @include maxbp($mega-menu-mobile) {
        display: block;
        position: absolute;
        top: 8px;
        right: $space-ten*2;
      }

      .cart--cart-block {
        .figure__image {
          background: url("/themes/custom/delta_theme/images/icons/src/black-cart.png") no-repeat;
          background-size: 100%;
          width: 24px;
          height: 24px;
          top: 0;
          right: 0;
        }
      }
    }
  }

  header.header {
    max-width: none;
    margin: auto;
    padding: 0 !important;
    float: right;
    width: auto;

    .block-container {
      @include maxbp($mega-menu-mobile) {
        padding: 0;
      }
    }

    @include maxbp($mega-menu-mobile) {
      float: none;
      top: 0;
      position: absolute;
      width: 100%;
      z-index: 1000000;
    }

    .main-nav-block {
      .search-wrapper {
        display: block;

        @include maxbp($mega-menu-mobile) {
          opacity: 0;
          visibility: hidden;
          position: absolute;
          background: $gray-90;
          float: none;
          width: 100%;
          z-index: 1;
          top: 40px;
          transition: all ease 0.5s;

          .serach-input {
            width: 100%;
            border-left: none;
            margin: 0;
          }
        }

        &.mobile-search-active {
          opacity: 1;
          visibility: visible;
        }
      }

      .mobile-header-icons {
        display: none;
        position: absolute;
        top: -2px;
        right: $space-ten*5;

        @include maxbp($mega-menu-mobile) {
          display: block;

          .mobile-serach-icon-block,
          .mobile-cart {
            display: inline-block;
          }

          .mobile-serach-icon-block {
            margin: 0 $space-ten*2 0 0;
            cursor: pointer;
          }
        }
      }
    }

    &.active-mobile-header {
      #block-languageswitchblock-2,
      #mobile-utility-menu,
      #block-useraccountmenu {
        display: block;
      }
    }

    #block-languageswitchblock-2 {
      @include maxbp($mega-menu-mobile) {
        margin: $space-ten*4 0 0;
        border-bottom: 1px solid #d7d7d9;

        .dropdown {
          a {
            display: block;
            text-align: left;
            padding: 8px 20px;
          }
        }
      }
    }

    #block-languageswitchblock-2,
    #mobile-utility-menu,
    #block-useraccountmenu {
      display: none;

      .utility-nav {
        .utility-nav-wrapper {
          ul {
            padding: 0;
            margin: 0;
            background: $gray-10;

            li {
              border-bottom: 1px solid $gray-70;
              display: block;
              position: relative;

              a {
                color: $white;
                text-decoration: none;
                display: block;
                padding: 8px $space-ten*2 $space-ten;
              }
            }
          }
        }
      }
    }
  }
}
