.footer_secondary {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1%;
  flex-direction: column-reverse;

  @include large {
    flex-direction: row;
    text-align: center;
  }

  &__nav {
    list-style: none;
    display: flex;
    padding: 0;
    flex-flow: column;
    margin: 0;

    @include xs {
      align-items: center;
    }

    @include large {
      flex-flow: row;
    }

    li {
      @include large {
        border-right: 1px solid $gray-60;
        border-left: 1px transparent;
        line-height: 1;

        &:last-child {
          border: 0 none;
        }
      }

      a {
        text-decoration: none;
        text-transform: uppercase;
        color: $gray-50;
        font-size: 10px;
        font-weight: bold;
        padding: 0 8px;
        letter-spacing: 1.5px;
        display: inline-block;
        line-height: normal;

        &:hover {
          color: $gray-10;
        }
      }
    }
  }

  .copy-rights {
    padding: 5px 8px 10px 15px;
  }

  @include large {
    .copy-rights {
      padding: 0 5px 0 15px;
    }
  }
}
