.left-icon-link {
  color: $delta-red;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;

  i,
  span {
    display: inline-block;
    vertical-align: middle;
  }
}

.right-icon-link {
  color: $delta-red;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;

  span {
    margin: 0 $space-ten 0 0;
  }

  i,
  span {
    display: inline-block;
    vertical-align: middle;
  }
}
