@include maxbp($mega-menu-mobile) {
  .mega-nav {
    .mega-menu-nav__item--sub {
      display: block !important;
      position: relative;
    }

    .mega-nav-wrapper#mega-nav-wrapper {
      position: relative;
      z-index: 999;
      height: auto;
      overflow: visible;
      top: 40px;
      left: 0;
      width: 100%;
      opacity: 0;

      > .mega-menu-nav {
        background: $white;
        opacity: 0;
        margin: 0;
        max-height: 0;
        overflow: hidden;
        transition: all ease 1s;
        padding: 0;

        > li {
          border-top: 1px solid $gray-70;
          display: block;
          position: relative;

          ul.mega-menu-nav--sub-1.mega-menu--sub-open {
            padding-top: $space-ten*2;
            padding-bottom: $space-ten*2;

            ul {
              padding-top: 0;
              padding-bottom: 0;
            }
          }

          &.mega-menu-nav__item--with-sub {
            //background: $gray-80;

            &.mega-menu--sub-parent-open {
              background: $gray-80;
            }

            > ul.mega-menu-nav--sub-1 {
              > li {
                > a {
                  font-weight: bold;
                }

                a {
                  color: $gray-10;
                  text-decoration: none;
                  display: block;
                  padding: $space-ten+10 $space-ten;
                }
              }
            }
          }

          > a {
            display: block;
            color: $gray-10;
            text-decoration: none;
            padding: $space-ten $space-ten*2;

            &:hover,
            &:active {
              background: $gray-80;
            }
          }
        }
      }

      &.main-nav--open {
        opacity: 1;

        > .mega-menu-nav {
          display: block;
          max-height: 1000px;
          opacity: 1;
        }
      }
    }

    .expand-sub {
      display: block;
      color: black;
      background: url("/themes/custom/delta_theme/images/icons/src/plus.svg") center center;
      height: 24px;
      width: 24px;
      position: absolute;
      right: $space-ten*2;
      text-align: center;
      top: 8px;

      &::after {
        display: none;
      }

      &.expand-sub--open {
        background: url("/themes/custom/delta_theme/images/icons/src/minus.svg");
      }
    }

    .mega-menu-nav--sub {
      max-height: 0;
      overflow: hidden;
      transition: all ease 1s;
      background: $gray-90;

      &.mega-menu--sub-open {
        max-height: 1000px;
      }
    }
  }
}

.toggle-expand {
  position: absolute;
  left: $space-ten*2;
  display: inline-block;
  cursor: pointer;
  top: -2px;
  width: auto;
  padding: 0;

  @include breakpoint($mega-menu-mobile) {
    display: none;
  }

  &.toggle-expand--open {
    background: none;

    .bar1 {
      -webkit-transform: rotate(-45deg) translate(-4px, 6px);
      transform: rotate(-45deg) translate(-4px, 6px);
    }

    .bar2 {
      opacity: 0;
    }

    .bar3 {
      -webkit-transform: rotate(45deg) translate(-4px, -8px);
      transform: rotate(45deg) translate(-4px, -8px);
    }
  }

  .bar1,
  .bar2,
  .bar3 {
    width: 24px;
    height: 2px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;
  }
}
