.search-wrapper {
  position: relative;
  float: right;

  .search-button {
    opacity: 0;
    visibility: hidden;
    transition: all ease 0.5s;
  }

  &.search-active {
    .serach-input {
      padding: $space-ten $space-ten+35 $space-ten $space-ten;
      background: $gray-90 -50px center;
      width: 300px;
      transition: all ease 0.7s;

      @include maxbp($big-desktop) {
        width: 250px;
      }
    }

    .search-button {
      opacity: 1;
      visibility: visible;
    }
  }

  .search-submit-block {
    position: absolute;
    right: $space-ten+5;
    top: $space-ten+5;
    margin: 0;
    height: 24px;
  }
}

// MOVE TO HEADER.SCSS
.right-header {
  float: right;

  @include maxbp($mega-menu-mobile) {
    float: none;
  }

  .mega-nav {
    float: left;
    margin: $space-ten 0 0;

    @include maxbp($mega-menu-mobile) {
      float: none;
    }
  }
}
