@mixin button($bg: $delta-red, $color: $white, $hoverBg: $delta-merlot, $hoverColor: $white, $font-family: $font-bold) {
  background-color: $bg;
  border: none;
  color: $color;
  cursor: pointer;
  font-family: $font-bold;
  padding: 0.875em 3.125em;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  transition: all ease 0.5s;
  letter-spacing: 2px;
  line-height: 19px;

  &:visited {
    color: $color;
  }

  &:hover {
    background-color: $hoverBg;

    [class*=" icon-"],
    [class^="icon-"]::before {
      color: $white;
      transition: all ease 0.5s;
    }
  }
}

@mixin primary {
  @include button($bg: $delta-red, $hoverBg: $delta-merlot);
}

@mixin secondary {
  @include button($gray-70, $black, $gray-50, $white);

  &:hover {
    background-color: $gray-50;
    color: $white;
  }
}

@mixin tertiary {
  @include button($transparent, $delta-red, $delta-merlot, $white);

  border: 1px solid $delta-red;

  &:hover {
    background-color: $delta-merlot;
    color: $white;
  }
}

@mixin disabled {
  @include button($gray-70, $gray-50);

  &:hover {
    background-color: $gray-70;
    color: $gray-50;
  }
}

@mixin button-lg {
  padding: 1.313em 3.5em;
  font-size: 1.125rem;
}

@mixin button-red {
  @include button($gray-10, $white, $delta-red);

  &:hover {
    background-color: $delta-red;
  }
}

.button,
.a .button {
  @include button;

  &--disabled {
    @include disabled;
  }

  &--primary {
    @include primary;
  }

  &--secondary {
    @include secondary;
  }

  &--tertiary {
    @include tertiary;
  }

  &--lg {
    @include button-lg;
  }
}

.button-red {
  @include button-red;
}
