.block-container {
  @include wrapper;
}

#utility-wrapper {
  background-color: $gray-10;

  @include maxbp($mega-menu-mobile) {
    display: none;
  }

  .utility {
    text-align: right;

    > div,
    > nav {
      display: inline-block;
      text-transform: uppercase;
      font-size: 13px;
      margin: 0 0 0 $space-ten/2;
    }

    nav.utility-nav {
      ul.utility-menu-nav {
        margin: 0;
        padding: 0;
      }

      li {
        display: inline-block;
        margin: 0;
        margin: 0 $space-ten/2 0 0;

        &:last-child {
          margin: 0;
        }

        a {
          color: white;
          text-decoration: none;
          display: block;
          padding: 8px $space-ten+5;
          transition: all ease 0.5s;
          background: transparent;

          &:hover {
            background: $delta-red;
          }
        }
      }
    }

    #block-delta-theme-account-menu {
      nav.utility-nav {
        li {
          &:last-child {
            display: none;
          }

          a[href="/user"] {
            //background: url("../../images/icons/src/account.svg") center center;
          }
        }
      }
    }

    #block-cart {
      transition: all ease 0.5s;
      background: transparent;
      padding: 7px;

      &:hover {
        background: $delta-red;
      }

      div.cart--cart-block {
        span {
          display: inline-block;
          vertical-align: middle;
        }
      }

      figure.cart--cart-block {
        img {
          width: 20px;
        }
      }
    }
  }
}
