.footer {
  clear: both;
  padding: 0;

  &__wrapper {
    background: $gray-80;

    @include medium {
      display: flex;
    }

    ul {
      list-style: none;
      padding-bottom: 1em;
      padding-top: 1em;
      margin: 0;

      @include medium {
        display: inline-flex;
        width: 20%;
        flex-direction: column;
      }

      h4 {
        margin: 0;
      }
    }
  }
}
