$breadcrumb-active: $gray-10;

.breadcrumb {
  @include list-reset;
}

.breadcrumb__item,
.breadcrumb__link,
.breadcrumb__link:link,
.breadcrumb__link:visited,
.breadcrumb__link:focus {
  color: $gray-10;
  text-decoration: none;
  font-size: 12px;
  transition: all ease 0.5s;
}

.breadcrumb__link {
  padding: 0 0 $space-ten/5;
  position: relative;
  margin: 0 $space-ten 0 0;

  &::after {
    content: "";
    background: $gray-10;
    height: 1px;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
  }

  &:hover {
    color: $delta_red;
    font-size: 14px;
    font-weight: bold;

    &::after {
      background: $delta_red;
    }
  }
}

.breadcrumb__item {
  display: inline-block;
  padding: 0 $space-ten 0 0;
  //font-size: 12px;

  &::after {
    content: "/";
  }

  &:last-child {
    color: $breadcrumb-active;

    &::after {
      content: "";
    }
  }
}

.breadcrumb_link:active {
  color: $breadcrumb-active;
}

.breadcrumb-block {
  &.dark-breadcrumb {
    background: $gray-10;
    padding: $space-ten 0;
    margin: $space-ten 0;

    .breadcrumb__item,
    .breadcrumb__link,
    .breadcrumb__link:link,
    .breadcrumb__link:visited,
    .breadcrumb__link:focus {
      color: $white;
    }

    .breadcrumb__link {
      &::after {
        background: $white;
      }

      &:hover {
        color: $delta_punch;
        font-size: 14px;
        font-weight: bold;

        &::after {
          background: $delta_punch;
        }
      }
    }
  }
}
